import request from "../utils/request"; // 获取code列表

export var getAllCode = function getAllCode(query) {
  return request({
    url: 'get_all_code',
    method: 'get',
    params: query
  });
}; // 删除code

export var deleteCode = function deleteCode(query) {
  return request({
    url: 'del_code',
    method: 'get',
    params: query
  });
}; // 编辑code

export var editCode = function editCode(query) {
  return request({
    url: 'edit_code',
    method: 'post',
    data: query
  });
}; // 新建code

export var addCode = function addCode(query) {
  return request({
    url: 'create_code',
    method: 'post',
    data: query
  });
};