import { getAllCode, deleteCode, editCode, addCode } from "../../api/code";
import { formatDate } from "../../utils/tool";
export default {
  name: 'CodeManagement',
  data: function data() {
    return {
      query: {
        code: '',
        pageIndex: 1,
        pageSize: 10
      },
      tableData: [],
      multipleSelection: [],
      delList: [],
      editVisible: false,
      pageTotal: 0,
      codeForm: {// code_name: '',
        // code: '',
        // code_num: '',
        // code_num: '',
        // energy_value: 0,
        // expirate_time: 0
      },
      idx: -1,
      id: -1,
      titleType: ''
    };
  },
  created: function created() {
    this.getData();
  },
  computed: {},
  methods: {
    expTime: function expTime(date) {
      date = parseInt(date);
      return formatDate(new Date(date));
    },
    getData: function getData() {
      var _this = this;

      getAllCode({
        code: this.query.code,
        page: this.query.pageIndex
      }).then(function (res) {
        // console.log('aaaaaaaaa', res.data);
        _this.tableData = res.data;
        _this.pageTotal = res.total;
      });
    },
    // 触发搜索按钮
    handleSearch: function handleSearch() {
      this.$set(this.query, 'pageIndex', 1);
      this.getData();
    },
    // 删除操作
    handleDelete: function handleDelete(index, row) {
      var _this2 = this;

      console.log(row.id); // 二次确认删除

      this.$confirm('确定要删除吗？', '提示', {
        type: 'warning'
      }).then(function () {
        deleteCode({
          id: row.id
        }).then(function (res) {
          if (res.code == 200) {
            _this2.$message.success('删除成功');

            _this2.tableData.splice(index, 1);
          }
        });
      }).catch(function () {
        _this2.$message.error('删除失败');
      });
    },
    // 多选操作
    handleSelectionChange: function handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // delAllSelection() {
    //     const length = this.multipleSelection.length;
    //     let str = '';
    //     this.delList = this.delList.concat(this.multipleSelection);
    //     for (let i = 0; i < length; i++) {
    //         str += this.multipleSelection[i].name + ' ';
    //     }
    //     this.$message.error(`删除了${str}`);
    //     this.multipleSelection = [];
    // },
    // 新建操作
    handleAdd: function handleAdd(type) {
      this.titleType = type == 'add' ? '新建' : '编辑';
      this.codeForm = {};
      this.editVisible = true;
    },
    // 编辑操作
    handleEdit: function handleEdit(index, row) {
      this.titleType = this.titleType == 'add' ? '新建' : '编辑';
      this.idx = index;
      this.codeForm = row; // console.log('row', row);

      this.editVisible = true;
    },
    // 保存新建
    saveAdd: function saveAdd() {
      var _this3 = this;

      this.$refs['codeForm'].validate(function (valid) {
        if (valid) {
          console.log('saveAdd', _this3.codeForm); // return;

          addCode(_this3.codeForm).then(function (res) {
            console.log('aaaaaaaaa', res);

            if (res.code == 200) {
              _this3.editVisible = false;

              _this3.$message.success("\u65B0\u5EFA\u6210\u529F"); // this.$set(this.tableData, this.total, this.codeForm);


              _this3.getData();
            }
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 保存编辑
    saveEdit: function saveEdit() {
      var _this4 = this;

      this.$refs['codeForm'].validate(function (valid) {
        if (valid) {
          // console.log('saveEdit', this.codeForm);
          editCode(_this4.codeForm).then(function (res) {
            // console.log('aaaaaaaaa', res.data);
            _this4.editVisible = false; // this.$message.success(`修改第 ${this.idx + 1} 行成功`);

            _this4.$message.success("\u4FEE\u6539\u6210\u529F");

            _this4.$set(_this4.tableData, _this4.idx, _this4.codeForm);
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 分页导航
    handlePageChange: function handlePageChange(val) {
      this.$set(this.query, 'pageIndex', val);
      console.log('val', this.query);
      this.getData();
    }
  }
};